@import url(https://fonts.googleapis.com/css?family=Exo);
body {
  margin: 0;
  padding: 0;
  width: 100%;
  border-collapse: collapse;
  font-family: "Exo", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}


.navigation-header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; 
    margin: 0 auto;
    width: 100%; 
    -webkit-align-content: center; 
            align-content: center;  
}

.navigation-logo {
    max-width: 80vw;
}

.navigation-basket-count {
    color: rgba(0,0,0,.8);
    font-weight: 800;
}

@media all and (min-width: 1024px) {
    .navigation-header {
        -webkit-flex-direction: row;
                flex-direction: row;
        box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
        max-height: 10vh;
        -webkit-justify-content: center;
                justify-content: center;
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
    }
    .navigation-logo {
        -webkit-justify-content: center;
                justify-content: center;
        max-width: 20vw;
        max-height: 8vh;
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
    }
    .navigation-basket {
        grid-column-start: 3;
        grid-column-end: 4;
        grid-row-start: 1;
    }   
    .navigation-basket {
        line-height: 5rem;
        font-size: 2rem;
    } 
    .navigation-basket-count {
        color: rgba(0,0,0,.8);
        font-weight: 800;
        font-size: 1.5rem;
        padding: .5rem; 
    }
}
.product-container {
    display: grid;
    -webkit-flex-direction: column;
            flex-direction: column;    
    grid-column-start: 2;
    grid-column-end: 3;
    margin: 1rem;
    background-color: rgba(0,0,0,.1);
}

.product-image {
    max-width: 80vw;
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}

.product-input-text {
    border: none;
    background-color: transparent;
    font-size: 1.5rem;
    margin: 1rem;
}

.product-container select{
    margin: 1rem;
}

.product-old-price {
    text-decoration: line-through;
}

.product-form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
}

@media all and (min-width: 1024px) {
    .product-container {
        -webkit-flex-direction: row;
                flex-direction: row;
        transition: .3s all ease;
    }
    .product-container:hover {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
        box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);        
    }
    .product-image {
        width: 20vw;
        margin: .5rem;
        border-radius: 3%;
    }

}
@media all and (min-width: 1024px) {
    .product-list-container {
        padding: .5rem;
        display: grid;
    }    
}


.basket-item {
    
}
.summary-header {
    display: grid;
    grid-column-start: 2;
    grid-column-end: 3;
}
.container-user-form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.user-form {    
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
    border-radius: 1%;
    padding: .5rem;
    width: 80vw;
}

.user-form-fieldset {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    grid-column-start: 2;
    grid-column-end: 3;
    -webkit-flex-direction: column;
            flex-direction: column;
    position: relative;
    border: none;
    border-radius: 1%;
    box-shadow: inset 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);   
    padding: 2.5rem;
    margin: .5rem;
}

.user-form-fieldset legend {
    color: rgba(0,0,0,.7);
    font-weight: 600;
    position: absolute;
    top: .5rem;
}

.user-form-input {
    border: none;
    background-color: rgba(0,0,0,.18);
    margin: .5rem;
    line-height: 1.5rem;
    width: 80%;
}

.user-form-input:hover {
    background-color: rgba(0,0,0,.36);
    box-shadow: 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}

.invalid {    
    border: 1px solid red;
}

.invalid-warning {
    visibility: hidden;
    font-size: .8rem;
    transition: .3s all ease;
}

.empty-form-warning {
    visibility: hidden;
    font-size: .8rem;
    transition: .3s all ease;
    padding: .5rem;
}

.active {
    visibility: visible;    
    color: red;
}

@media all and (min-width: 1024px) {
    .user-form {       
        width: 30vw;
    }
  }
  


span {
  color: rgba(0,0,0,.6);
}

a,
a:hover {
  text-decoration: none;
  color: rgba(0,0,0,.7);
}

.App {
  text-align: center;
  color: rgba(0,0,0,.7);
  margin-top: 0;
  width: 100%;
  position: relative;
}

.container {
  display: grid;
  grid-template-columns: 1fr 8fr 1fr;
  grid-column-start: 2;
  grid-column-end: 3;
}

.form-label {
  color: rgba(0,0,0,.8);
  font-weight: 500;
  font-size: 1rem;
}

.header-2 {
  color: rgba(0,0,0,.8);
  font-weight: 800;
}

.header-3 {
  color: rgba(0,0,0,.8);
  font-weight: 600;
}

.submit-button {
  border: none;
  padding: .5rem;
  color: rgba(0,0,0,.7);
  background-color: rgba(0,0,0,.18);
  box-shadow:  0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
  border-radius: 10%;
}

@media all and (min-width: 1024px) {
  .submit-button:hover {
    background-color: rgba(0,0,0,.36);
    box-shadow:  inset 0 5px 11px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
  }
}



